import React from 'react';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import QRCodeGenerator from './QRCodeGenerator';
import QRCodeScanner from './QRCodeScanner'; // QRCodeScanner   import

const App = () => {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<QRCodeGenerator />} /> {/* QRCodeGenerator  */}
        <Route path="/qrcodescanner" element={<QRCodeScanner />} /> {/* QRCodeScanner  */}
      </Routes>
    </Router>
  );
};

export default App;