// export default QRCodeScanner;

import React, { useState, useEffect } from 'react';
import QrScanner from 'react-qr-scanner';
import { Link } from 'react-router-dom';

const QRCodeScanner = () => {
  const [scannedData, setScannedData] = useState(null);
  const [showData, setShowData] = useState(false);

  const handleScan = (result) => {
    if (result) {
      setScannedData(result.text);
      setShowData(true);

      // 5   
      setTimeout(() => {
        setShowData(false);
      }, 3000);
    }
  };

  const handleError = (error) => {
    console.error(error);
  };

  const handleRescan = () => {
    setScannedData(null);
    setShowData(false);
  };

  useEffect(() => {
    //  unmount   .
    return () => {
      clearTimeout();
    };
  }, []);

  return (
    <div className='qr-scan-container' style={{ marginLeft: '10px' }}>
      <h1 style={{ marginLeft: '10px' }}>QR Code Scanner</h1>
      <QrScanner
        delay={300}
        onError={handleError}
        onScan={handleScan}
        style={{ width: '50%' }}
      />
      <div style={{ marginTop: '20px', fontSize: '20px' }}>
        {showData && scannedData && (
          <>
            {scannedData.includes('Name') &&
            scannedData.includes('Number') &&
            scannedData.includes('Part') ? (
              <>
                <p style={{ fontSize: '25px', fontFamily: 'NanumGothic', fontWeight: 'bold' }}>
                  SIREN {scannedData.match(/Number: ([\w\s]+)/)[1]} {scannedData.match(/Part: ([\w\s]+)/)[1]} {scannedData.match(/Name: ([\w\s]+)/)[1]}
                </p>
                <p style={{ fontSize: '25px', fontFamily: 'NanumGothic', fontWeight: 'bold' }}>
                  SIREN 22     !
                </p>
              </>
            ) : (
              <p>  .</p>
            )}
          </>
        )}
      </div>
      <button onClick={handleRescan} style={{ fontSize: '20px', marginTop: '20px' }}>
        QR   
      </button>
      <Link
        to="/"
        style={{
          fontSize: '25px',
          textDecoration: 'none',
          marginTop: '20px',
          display: 'block',
          fontFamily: 'NanumGothic',
          fontWeight: 'bold',
        }}
      >
        QR  
      </Link>
    </div>
  );
};

export default QRCodeScanner;
